import React from 'react'
import { Controller } from 'react-hook-form'
import { TextField, InputAdornment } from '@material-ui/core'
import IconSearch from '../../../../assets/Icons/IconSearch/IconSearch'
import useStyles from '../Filters.style'

function TextFieldSearch({
  value,
  control,
  onChange,
  placeholder,
  error,
  helperText,
  isLong,
  isXL,
  isMedium,
  isXSL,
  isSmallMedium,
}) {
  const classes = useStyles()
  return (
    <Controller
      name="search"
      control={control}
      render={({ field }) => (
        <TextField
          {...field}
          id="search_field"
          value={value}
          onKeyPress={(e) => e.key === 'Enter' && e.preventDefault()}
          onChange={onChange}
          className={
            // eslint-disable-next-line no-nested-ternary
            isLong
              ? `${classes.search__input} ${classes.search__input_long}`
              : // eslint-disable-next-line no-nested-ternary
              isXL
              ? `${classes.search__input} ${classes.search__input_XL}`
              : // eslint-disable-next-line no-nested-ternary
              isXSL
              ? `${classes.search__input} ${classes.search__input_XSL}`
              : // eslint-disable-next-line no-nested-ternary
              isMedium
              ? `${classes.search__input} ${classes.search__input_medium}`
              : isSmallMedium
              ? `${classes.search__input} ${classes.search__input_small_medium}`
              : classes.search__input
          }
          variant="outlined"
          placeholder={placeholder}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <IconSearch />
              </InputAdornment>
            ),
          }}
          error={error}
          helperText={helperText}
        />
      )}
    />
  )
}

export default React.memo(TextFieldSearch)
