import * as React from 'react'
import Tooltip from '@mui/material/Tooltip'
import { Avatar, Box } from '@mui/material'
import useStyles from './StudentInfoAvatar.style'

interface StudentInfoAvatarProps {
  firstName: string
  lastName: string
  avatar: string
}

const StudentInfoAvatar = React.forwardRef(({ firstName, lastName, avatar }: StudentInfoAvatarProps, ref) => {
  const classes = useStyles()
  return (
    <Box className={classes.root} ref={ref}>
      <Avatar alt={`${firstName} ${lastName}`} src={avatar} sx={{ width: '36px', height: '36px' }} />
      <Tooltip
        title={`${firstName} ${lastName}`}
        classes={{ tooltip: classes.tooltip }}
        enterDelay={1000}
        enterNextDelay={1000}
        leaveDelay={100}
      >
        {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */}
        <span className={classes.text}>{`${firstName} ${lastName}`}</span>
      </Tooltip>
    </Box>
  )
})

export default StudentInfoAvatar
