import React from 'react'
import { Box, Fab, Typography } from '@mui/material'
import { ThemeProvider } from '@mui/system'
import IconInc from '../../../../assets/Icons/IconInc/IconInc'
import IconMinus from '../../../../assets/Icons/IconMinus/IconMinus'

import theme from '../../../../utils/themeMUI5'

const ButtonCounter = ({ labelTitle, value, onChange }) => {
  const handleDecrement = () => {
    onChange(value - 1)
  }

  const handleInccrement = () => {
    onChange(value + 1)
  }

  return (
    <ThemeProvider theme={theme}>
      <Box sx={{ display: 'flex', flexDirection: 'column' }} mb="15px">
        <Typography variant="body2" mb="15px" sx={{ color: '#000' }}>
          {labelTitle}
        </Typography>
        <Box sx={{ display: 'flex', width: 108, justifyContent: 'space-between', alignItems: 'center' }}>
          <Fab color="white" onClick={handleDecrement} sx={{ width: 28, height: 28, minHeight: 28 }}>
            <IconMinus />
          </Fab>
          <Typography variant="body4" sx={{ color: '#000' }}>
            {value}
          </Typography>
          <Fab color="white" onClick={handleInccrement} sx={{ width: 28, height: 28, minHeight: 28 }}>
            <IconInc />
          </Fab>
        </Box>
      </Box>
    </ThemeProvider>
  )
}

export default ButtonCounter
