import React, { useEffect, useState } from 'react'
import { InputAdornment, Typography, FormHelperText, FormControl, Button } from '@material-ui/core'
import { Controller } from 'react-hook-form'
import { ControlledTextField, ControlledSelectField } from '../../FormElements'
import Dropzone from '../../Dropzone/Dropzone'
import useStyles from './EditProfilePopup.styles'
import ButtonCounter from '../../../Main/OnboardingPage/ButtonCounter/ButtonCounter'
import { PPExpType, PPSpendTime } from '../../../../services/types/onboarding/onboarding-types'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { getOnboardingBaseData } from '../../../../services/axios/onboarding'

function SecondTabProfile({ values }) {
  const { control, errors, types, times } = values

  const getTimeDirItems = (val) => {
    let name
    if (!val?.max) name = `> ${val.min} часов`
    else name = `${val.min} - ${val.max} часов`
    return { id: val.id, name }
  }
  const classes = useStyles()

  return (
    <fieldset className={classes.fieldsetThirdTab}>
      <Controller
        name="pet_project_experience_type"
        control={control}
        render={({ field: { name, value, onChange } }) => (
          <ControlledSelectField
            labelTitle="Участвовал ли ты в каком-либо проекте по специальности, которой сейчас обучаешься?"
            tipContent="Выбери из списка"
            value={value || []}
            onChange={onChange}
            error={!!errors?.pet_project_experience_type?.message}
            helperText={errors?.pet_project_experience_type?.message}
            items={types?.map((t) => ({ id: t?.id, name: t?.value }))}
            maxWidth={386}
          />
        )}
      />
      <Controller
        name="pet_project_spend_time"
        control={control}
        render={({ field: { name, value, onChange } }) => (
          <ControlledSelectField
            labelTitle="Сколько часов в неделю ты готов уделять участию в Pet-проекте или проекте Мастерской? "
            tipContent="Выбери из списка"
            value={value || []}
            onChange={onChange}
            error={!!errors?.pet_project_spend_time?.message}
            helperText={errors?.pet_project_spend_time?.message}
            items={times?.map(getTimeDirItems)}
            maxWidth={386}
          />
        )}
      />
      <Controller
        name="pet_project_count"
        control={control}
        render={({ field: { name, value, onChange } }) => (
          <ButtonCounter
            labelTitle="Кол-во Pet-проектов, в которых ты сейчас принимаешь участие"
            value={value || 0}
            onChange={onChange}
          />
        )}
      />
      <ControlledTextField
        labelTitle="Твои ожидания от проектов"
        fieldName="pet_project_dreams"
        placeholder="Расскажи, зачем ты принимаешь в них участие"
        control={control}
        error={!!errors.pet_project_dreams?.message}
        helperText={errors.pet_project_dreams?.message}
        maxWidth={386}
        multiline
        rows={5}
      />
    </fieldset>
  )
}

export default React.memo(SecondTabProfile)
