import React, { useMemo, useEffect, useState, FC } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'

import debounce from 'lodash.debounce'
import { Controller, useForm } from 'react-hook-form'
import { Box, Button, DialogContent, DialogTitle, Stack, ThemeProvider, Typography } from '@mui/material'
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace'
import { DataGrid, GridCellParams, GridRowsProp, MuiEvent } from '@mui/x-data-grid'
import { AxiosError } from 'axios'
import LoadingOverlay from '../../../components/Sub/DataTable/LoadingOverlay/LoadingOverlay'
import TextFieldSearch from '../../../components/Sub/Filters/textfield-search/textfield-search'
import useStyles from './AboutVacancyPage.styles'
import theme from '../../../utils/themeMUI5'
import { titleIDs, getInfoColumns } from './columns'
import useAsync from '../../../hooks/useAsync'
import api from '../../../api/api'
import { API_HR_PART_EXPORT_INFO_ENDPOINT } from '../../../utils/constants'
import {
  IHRVacancyHeader,
  IStudentStatus,
  TDataBlobError,
  TDownloadFile,
  TGetCellClassName,
  TGetRowHeight,
} from '../../../services/types/vacacies-page-types/vacancies-page-types'
import {
  clearPatchError,
  setIsInfoPopupOpen,
  setIsOpenFoundJobPopup,
  setIsOpenVacancyAcceleratePopup,
  setIsOpenVacancyPopup,
  setStudentToEdit,
  setVacancyToDelete,
  setVacancyToEdit,
} from '../../../services/slices/vacancies-page-slice'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import {
  getDirectories,
  getInfo,
  getStudentProfile,
  patchStudentStatus,
  patchVacancyInfo,
} from '../../../services/axios/vacancies-page'
import { ControlledSelectField, ControlledTextField } from '../../../components/Sub/FormElements'
import { EditProfilePopup, FoundJobPopup } from '../../../components/Sub/GlobalPopups'
import VacancyPopup from '../../../components/Main/VacanciesListPage/VacancyPopup/VacancyPopup'
import { TSetSnack } from '../../../services/types/global-types'
import InfoPopup from '../VacanciesListPageNew/InfoPopup/InfoPopup'
import VacationActionPopup from '../../../components/Sub/GlobalPopups/VacationActionPopup/VacationActionPopup'
import AccelerationPopup from '../../../components/Sub/GlobalPopups/AccelerationPopup/AccelerationPopup'
import { enqueueSnackbar } from '../../../services/slices/app-slice'

type Props = {
  setSnack: TSetSnack
}

const getRowHeight: TGetRowHeight = ({ id }) => (titleIDs.includes(id.id) ? 32 : 64)

const getCellClassName: TGetCellClassName = ({ row, field }) => {
  if (titleIDs.includes(row.id as string)) {
    if (field === 'id') {
      return 'title'
    }
  }
  return ''
}

const VacancyInfoPopup: FC<Props> = ({ setSnack }) => {
  const location = useLocation()
  const navigate = useNavigate()
  const dispatch = useAppDispatch()
  const vacationOpenState = useState<boolean>(false)
  const [, setVacationOpenState] = vacationOpenState
  const {
    directoriesData,
    vacancyToEdit,
    isLoading,
    isOpenFoundJobPopup,
    rows,
    header,
    studentProfile,
    isOpenVacancyPopup,
    vacancyToArchive,
    studentToEdit,
    isNetworkError,
    patchNetworkError,
    networkError,
  } = useAppSelector((store) => store.vacanciesPage)

  const statusItems = directoriesData?.statuses?.map((item) => ({ id: item.label, name: item.label })) || []
  const sourceItems = directoriesData?.sourcesItems?.map((item) => ({ id: item.id, name: item.name })) || []
  const profilesItems = directoriesData?.profilesItems?.map((item) => ({ id: item.id, name: item.name })) || []

  const [student, setStudent] = useState('')
  const [searchQuery, setSearchQuery] = useState('')
  const classes = useStyles()
  const [editPopupOpen, setEditPopupOpen] = useState(false)

  const {
    control,
    reset,
    watch,
    formState: { errors },
  } = useForm({
    defaultValues: {
      position: header?.position,
      company_name: header?.company_name,
      vacancy_status: header?.vacancy_status || null,
      source: header?.source || null,
      hr_profile: header?.hr_profile,
      comment: header?.comment,
    },
    mode: 'onChange',
  })

  // Защищаем страницу от перехода не через кнопки
  useEffect(() => {
    if (!location.state?.company && !location.state?.notifications && !location.state?.vacancies) navigate(-1)
  }, [])

  const getDirectoriesOnPage = async () => {
    try {
      await dispatch(getDirectories())
    } catch {
      return undefined
    }
    return undefined
  }

  useEffect(() => {
    reset({
      position: header?.position,
      company_name: header?.company_name,
      vacancy_status: header?.vacancy_status || null,
      source: header?.source || null,
      hr_profile: header?.hr_profile,
      comment: header?.comment,
    })
  }, [header])

  useEffect(() => {
    if (location.state?.company) {
      getDirectoriesOnPage()
    }
  }, [header])

  useEffect(() => {
    if (location.state?.company || location.state?.notifications) {
      getDirectoriesOnPage()
    }
  }, [location.state])

  const { run: runDownload } = useAsync({
    data: [],
    error: null,
  })

  const handleClose = () => {
    dispatch(setIsInfoPopupOpen(false))
    dispatch(setVacancyToEdit(null))
    navigate(-1)
  }

  const handleEditClick = () => {
    if (location.state?.company || location.state?.notifications) dispatch(setVacancyToEdit(header))
    dispatch(setIsOpenVacancyPopup(true))
  }

  const handleDeleteClick = () => {
    dispatch(setVacancyToDelete(header.id))
  }

  const patchStudentStatusOnPopup = async (studentId: number, status: IStudentStatus) => {
    try {
      await dispatch(patchStudentStatus({ status, studentId }))
    } catch {
      return undefined
    }
    return undefined
  }

  const patchInfo = async () => {
    const data = { ...watch() }
    try {
      await dispatch(patchVacancyInfo({ id: vacancyToEdit?.id, data }))
    } catch {
      return undefined
    }
    return undefined
  }

  const patchInfoStatus = async (status: string) => {
    const data = { ...watch() }
    const dataWithNewStatus = { ...data, vacancy_status: status }
    try {
      await dispatch(patchVacancyInfo({ id: vacancyToEdit?.id, data: { ...dataWithNewStatus } }))
    } catch {
      return undefined
    }
    return undefined
  }

  const patchStatus = (studentId: number, status: IStudentStatus) => {
    switch (status.status) {
      case 'Испытательный срок':
        dispatch(setStudentToEdit({ id: studentId }))
        // Закрываем текущий попап и открываем новый
        dispatch(setIsInfoPopupOpen(false))
        dispatch(setIsOpenFoundJobPopup(true))
        return
      case 'В отпуске':
        dispatch(setStudentToEdit(rows.find((item) => item?.id === studentId)))
        setVacationOpenState(true)
        return
      case 'Акселерация':
        dispatch(setStudentToEdit(rows.find((item) => item?.id === studentId)))
        dispatch(setIsOpenVacancyAcceleratePopup(true))
        return
      default:
        patchStudentStatusOnPopup(studentId, status)
    }
  }

  const columns = getInfoColumns(directoriesData?.studentStatuses, patchStatus)

  const getInfoOnPopup = async () => {
    if (vacancyToEdit) {
      try {
        await dispatch(getInfo({ searchQuery, id: vacancyToEdit.id }))
      } catch {
        return undefined
      }
    }
    return undefined
  }

  const downloadFile: TDownloadFile = async (apiUrl, type, filename) => {
    try {
      const response = await api.get(apiUrl, { responseType: 'blob' })
      const blob = new Blob([response?.data], { type })
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', filename)
      document.body.appendChild(link)
      link.click()
      return Promise.resolve(response?.data)
    } catch (e) {
      const err = e as AxiosError
      const isJsonBlob = (data: TDataBlobError) => data instanceof Blob && data.type === 'application/json'
      const responseData = isJsonBlob(err.response?.data) ? await err.response?.data?.text() : err.response?.data || {}
      const responseJson = typeof responseData === 'string' ? JSON.parse(responseData) : responseData
      return Promise.reject(responseJson)
    }
  }

  const exportFile = () => {
    if (vacancyToEdit)
      runDownload(
        downloadFile(API_HR_PART_EXPORT_INFO_ENDPOINT(vacancyToEdit.id), 'application/octet-stream', 'export.zip'),
      )
  }

  const changeSearchQuery = (v: string) => {
    if (v === '' || v.length > 2) setSearchQuery(v)
  }

  const debouncedChangeSearchQuery = useMemo(() => debounce(changeSearchQuery, 300), [])

  useEffect(() => () => debouncedChangeSearchQuery.cancel(), [])

  useEffect(() => {
    debouncedChangeSearchQuery(student)
  }, [student])

  useEffect(() => {
    getInfoOnPopup()
  }, [searchQuery, isOpenFoundJobPopup, isOpenVacancyPopup])

  const handleStudentClick = async (params: GridCellParams, event: MuiEvent) => {
    event.defaultMuiPrevented = true
    await dispatch(getStudentProfile(params?.row?.id))
    setEditPopupOpen(true)
  }

  useEffect(() => {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    if (patchNetworkError?.status === 400 || networkError?.status === 400) {
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'error',
            title: 'Нельзя сменить статус',
            message: 'Для смены нужно заполнить все поля и источник лида в вакансии со статусом «Черновик»',
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      dispatch(clearPatchError())
    }
  }, [isNetworkError])

  return (
    <>
      <FoundJobPopup
        setPrevPopup={() => null}
        setSnack={setSnack}
        openState={[isOpenFoundJobPopup, (status: boolean) => dispatch(setIsOpenFoundJobPopup(status))]}
        vacancy={vacancyToEdit}
        studentToEdit={studentToEdit}
        updateVacancies={getInfoOnPopup}
      />
      <VacationActionPopup
        openState={vacationOpenState}
        setSnack={setSnack}
        studentToEdit={studentToEdit as any}
        isVacancyPage
      />
      <AccelerationPopup isVacancyPage />
      <EditProfilePopup
        openState={[editPopupOpen, setEditPopupOpen]}
        setSnack={undefined}
        studentToEdit={studentProfile}
      />
      <VacancyPopup
        openState={[isOpenVacancyPopup, (status: boolean) => dispatch(setIsOpenVacancyPopup(status))]}
        setSnack={setSnack}
      />
      <InfoPopup
        title={vacancyToArchive ? 'Перенести в архив' : 'Удалить вакансию'}
        subtitle={
          vacancyToArchive
            ? 'Если вакансия перенесена в архив, то студенты больше не смогут на неё откликаться'
            : 'Ты действительно хочешь удалить вакансию?'
        }
      />
      <ThemeProvider theme={theme}>
        <Box sx={{ maxWidth: '1280px', margin: '0 auto', height: '1200px', padding: '16px 40px 0 40px' }}>
          {/* eslint-disable-next-line @typescript-eslint/ban-ts-comment */}
          {/* @ts-ignore */}
          <Button
            variant="text"
            onClick={handleClose}
            sx={{
              width: 'fit-content',
              outline: 'none!important',
              backgroundColor: 'transparent!important',
              color: '#1A1B22!important',
              fontSize: 16,
              padding: '0!important',
            }}
          >
            <KeyboardBackspaceIcon sx={{ mr: '12px' }} />
            {/* eslint-disable-next-line no-nested-ternary */}
            {location.state?.company
              ? 'Информация о компании'
              : location.state?.notifications
              ? 'Назад'
              : `Список вакансий / ${header?.position}`}
          </Button>
          <DialogTitle component="div">
            <div className={classes.header}>
              <div className={classes.info}>
                <Typography variant="h2" component="h2">
                  Информация о вакансии
                </Typography>
              </div>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: '20px' }}>
                <Button variant="outlined" className={classes.deleteButton} onClick={handleDeleteClick}>
                  Удалить вакансию
                </Button>
                <Button color="primary" sx={{ width: 209 }} onClick={handleEditClick}>
                  Редактировать вакансию
                </Button>
              </Box>
            </div>
            <Box
              sx={{
                width: '100%',
                marginTop: '32px',
                display: 'grid',
                gridTemplateColumns: '420px 420px 288px',
                columnGap: '36px',
                rowGap: '16px',
              }}
            >
              <ControlledTextField
                labelTitle="Название вакансии"
                fieldName="position"
                control={control}
                error={!!errors.position?.message}
                helperText={errors.position?.message}
                placeholder="Укажи название вакансии"
                onBlur={patchInfo}
              />
              <ControlledTextField
                labelTitle="Название компании"
                fieldName="company_name"
                control={control}
                error={!!errors.company_name?.message}
                helperText={errors.company_name?.message}
                placeholder="Укажи название компании"
                onBlur={patchInfo}
              />
              <Controller
                name="vacancy_status"
                control={control}
                render={({ field: { name, value, onChange } }) => (
                  <ControlledSelectField
                    labelTitle="Статус вакансии"
                    tipContent="Выбери из списка"
                    value={value}
                    onChange={(e) => {
                      // onChange(e)
                      patchInfoStatus(e?.target?.value)
                    }}
                    error={!!errors?.vacancy_status?.message}
                    helperText={errors?.vacancy_status?.message}
                    items={statusItems}
                    maxWidth={288}
                  />
                )}
              />
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  gap: '20px',
                }}
              >
                <Controller
                  name="source"
                  control={control}
                  render={({ field: { name, value, onChange } }) => (
                    <ControlledSelectField
                      labelTitle="Источник лида"
                      tipContent="Выбери из списка"
                      value={value}
                      onChange={(e) => {
                        onChange(e)
                        patchInfo()
                      }}
                      error={!!errors?.source?.message}
                      helperText={errors?.source?.message}
                      items={sourceItems}
                      maxWidth={420}
                    />
                  )}
                />
                <Controller
                  name="hr_profile"
                  control={control}
                  render={({ field: { name, value, onChange } }) => (
                    <ControlledSelectField
                      labelTitle="Ответственный менеджер"
                      tipContent="Выбери из списка"
                      value={value || []}
                      onChange={(e) => {
                        onChange(e)
                        patchInfo()
                      }}
                      error={!!errors?.hr_profile?.message}
                      helperText={errors?.hr_profile?.message}
                      items={profilesItems}
                      maxWidth={420}
                    />
                  )}
                />
              </Box>
              <ControlledTextField
                labelTitle="Комментарий"
                fieldName="comment"
                control={control}
                error={!!errors.comment?.message}
                helperText={errors.comment?.message}
                placeholder="Добавь комментарии от работодателя или заметку о самой вакансии "
                multiline
                rows={4}
                symbolsCount={watch('comment')?.length}
                maxLength={500}
                minLength={1}
                maxWidth={747}
                textClass={classes.textArea}
                onBlur={patchInfo}
              />
            </Box>
          </DialogTitle>
          <DialogContent>
            <Typography variant="body4" component="h3" sx={{ width: '100%', color: '#1A1B22', marginTop: '55px' }}>
              Список студентов с откликами
            </Typography>
            <Box className={classes.header} sx={{ marginTop: '26px', width: '100%' }}>
              <TextFieldSearch
                value={student}
                control={control}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => setStudent(e?.target?.value)}
                placeholder="Поиск студента"
                error={undefined}
                helperText={undefined}
                isLong={undefined}
                isXL={undefined}
                isMedium={undefined}
                isXSL={undefined}
                isSmallMedium={undefined}
              />
              <Button color="secondary" sx={{ width: 169, marginRight: '1px' }} onClick={exportFile}>
                Выгрузить отклики
              </Button>
            </Box>
            <Box
              sx={{
                height: 460,
                width: '100%',
                marginTop: '43px',
                '& .title': {
                  fontSize: 13,
                  fontWeight: 400,
                  lineHeight: '16px',
                  padding: '0 !important',
                  justifyContent: 'center !important',
                  alignItems: 'center !important',
                  backgroundColor: '#F1F6FF',
                },
              }}
            >
              <DataGrid
                onCellClick={handleStudentClick}
                sx={{
                  '& .MuiDataGrid-cell': { cursor: 'pointer' },
                }}
                rows={rows as GridRowsProp}
                columns={columns}
                pageSize={100}
                rowsPerPageOptions={[5]}
                disableSelectionOnClick
                disableColumnMenu
                disableColumnSelector
                getRowHeight={(id) => getRowHeight({ id })}
                getCellClassName={getCellClassName}
                loading={isLoading}
                components={{
                  NoRowsOverlay: () => (
                    <Stack height="100%" alignItems="center" justifyContent="center">
                      Нет данных
                    </Stack>
                  ),
                  NoResultsOverlay: () => (
                    <Stack height="100%" alignItems="center" justifyContent="center">
                      Local filter returns no result
                    </Stack>
                  ),
                  ErrorOverlay: () => (
                    <Stack height="100%" alignItems="center" justifyContent="center">
                      Error
                    </Stack>
                  ),
                  LoadingOverlay,
                }}
              />
            </Box>
          </DialogContent>
        </Box>
      </ThemeProvider>
    </>
  )
}

export default React.memo(VacancyInfoPopup)
