import React, { FC } from 'react'
import { Typography } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import { useNavigate } from 'react-router-dom'
import useStyles from './InfoPopup.styles'
import { IInfoPopup } from '../../../../services/types/vacacies-page-types/vacancies-page-types'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import {
  setIsInfoPopupOpen,
  setVacancyToArchive,
  setVacancyToDelete,
  setVacancyToEdit,
} from '../../../../services/slices/vacancies-page-slice'
import { deleteVacancy, patchVacancyStatus } from '../../../../services/axios/vacancies-page'

const InfoPopup: FC<IInfoPopup> = ({ title, subtitle }) => {
  const classes = useStyles()
  const dispatch = useAppDispatch()
  const navigate = useNavigate()
  const { vacancyToArchive, vacancyToDelete } = useAppSelector((store) => store.vacanciesPage)

  const handleClose = () => {
    dispatch(setVacancyToArchive(null))
    dispatch(setVacancyToDelete(null))
  }

  const onSubmit = async () => {
    try {
      if (vacancyToArchive) {
        await dispatch(patchVacancyStatus({ vacancyId: vacancyToArchive, status: { status: 'Архив' } }))
        dispatch(setVacancyToArchive(null))
      } else if (typeof vacancyToDelete === 'number') {
        await dispatch(deleteVacancy(vacancyToDelete))
        dispatch(setIsInfoPopupOpen(false))
        dispatch(setVacancyToEdit(null))
        dispatch(setVacancyToDelete(null))
        navigate(-1)
      }
    } catch {
      return undefined
    }
    return undefined
  }

  return (
    <div>
      <Dialog classes={{ paper: classes.root }} open={!!vacancyToArchive || !!vacancyToDelete} onClose={handleClose}>
        <div className={classes.container}>
          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} id="form-consult">
              <div className={classes.titleContainer}>
                <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                  {title}
                </Typography>
                <Typography className={classes.subtitle}>{subtitle}</Typography>
              </div>
            </form>
            <div className={classes.buttonContainer}>
              <Button type="button" className={classes.previousButton} onClick={handleClose}>
                Отмена
              </Button>
              <Button className={classes.submitButton} type="button" onClick={onSubmit}>
                {vacancyToArchive ? 'В архив' : 'Удалить'}
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}

export default React.memo(InfoPopup)
