import React from 'react'
import { Box } from '@mui/system'
import { Typography } from '@mui/material'
import { Control } from 'react-hook-form'
import telegramIcon from '../../../../assets/telegram-icon.svg'
import emailIcon from '../../../../assets/email-icon.svg'
import ControlledTextField from '../controlled-text-field/controlled-text-field'

interface Props {
  variant: 'telegram' | 'email' | 'whatsapp'
  width?: number
  control: Control<any>
  error: string
  name: string
  placeholder: string
}

const ControlledSocialContact = ({ variant, width, control, error, name, placeholder }: Props) => {
  let icon
  let networkTitle
  switch (variant) {
    case 'telegram':
      icon = telegramIcon
      networkTitle = 'Telegram'
      break
    case 'email':
      icon = emailIcon
      networkTitle = 'Email'
      break
    case 'whatsapp':
      icon = telegramIcon
      networkTitle = 'WhatsApp'
      break
    default:
      icon = telegramIcon
  }
  return (
    <Box
      sx={{
        display: 'flex',
        width: { width },
        justifyContent: 'space-between',
      }}
    >
      <Box
        sx={{
          width: 148,
          height: 40,
          borderRadius: '4px',
          outline: '1px solid #DDE0E4',
          padding: '10px 13px',
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <img
          src={icon}
          alt="Social network icon"
          style={{ display: 'block', width: 24, height: 24, marginRight: '7px' }}
        />
        <Typography variant="body1">{networkTitle}</Typography>
      </Box>
      <ControlledTextField
        fieldName={name}
        control={control}
        error={!!error}
        helperText={error}
        placeholder={placeholder}
        maxWidth={226}
      />
    </Box>
  )
}

export default ControlledSocialContact
