const useButtonClasses = makeStyles(() => ({
  iconContainer: {
    padding: 8,
    '&:hover $icon': {
      fill: '#1d6bf3',
    },
  },
  icon: {
    fill: '#797981',
  },
}))

interface IButtonHover {
  icon: React.ReactElement
  title: string
  disabled?: boolean
  onClick?: () => void
}

const ButtonHover = ({ icon, title, disabled, onClick }: IButtonHover) => {
  const classes = useButtonClasses()
  return (
    <Tooltip title={title}>
      <IconButton className={classes.iconContainer} disabled={disabled} onClick={onClick}>
        {React.cloneElement(icon, { className: classes.icon })}
      </IconButton>
    </Tooltip>
  )
}
