import React, { useEffect, useState } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { IconButton } from '@material-ui/core'
import { Typography } from '@mui/material'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import { format } from 'date-fns'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import useStyles from './AccelerationPopup.style'
import { formatApiDate } from '../../../../utils/dates'
import { ControlledDateField, ControlledSelectField } from '../../FormElements'
import { accelerationScheme } from '../../../../utils/schemas'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setIsOpenAcceleratePopup } from '../../../../services/slices/companies-page-slice'
import { patchStudentStatus } from '../../../../services/axios/companies-page'
import { patchStudentStatus as patchVacancyStudentStatus } from '../../../../services/axios/vacancies-page'
import { setIsOpenVacancyAcceleratePopup } from '../../../../services/slices/vacancies-page-slice'

const AccelerationPopup = ({ isVacancyPage = false }) => {
  const classes = useStyles()
  const [modifiedAt, setModifiedAt] = useState(Date.now())
  const dispatch = useAppDispatch()
  const { studentToEdit, isOpenAcceleratePopup } = useAppSelector((store) => store.companiesPage)
  const { studentToEdit: vacancyStudentToEdit, isOpenVacancyAcceleratePopup } = useAppSelector(
    (store) => store.vacanciesPage,
  )
  const defaultValues = {
    modified_at: format(Date.now(), 'yyyy-MM-dd'),
  }
  const {
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm({ defaultValues, resolver: yupResolver(accelerationScheme), mode: 'onChange' })

  const handleClose = () => {
    if (isVacancyPage) {
      dispatch(setIsOpenVacancyAcceleratePopup(false))
    } else {
      dispatch(setIsOpenAcceleratePopup(false))
    }
    reset()
  }

  const dateChangeHandler = (date) => {
    setValue('modified_at', date, { shouldValidate: true, shouldDirty: true })
    setModifiedAt(date)
  }

  const onSubmit = async (data) => {
    if (isVacancyPage) {
      await dispatch(
        patchVacancyStudentStatus({
          studentId: vacancyStudentToEdit?.id,
          status: { status: 'Акселерация' },
          modified_at: formatApiDate(new Date(data.modified_at)),
        }),
      )
      handleClose()
    } else {
      await dispatch(
        patchStudentStatus({
          studentId: studentToEdit?.id,
          status: { status: 'Акселерация' },
          modified_at: formatApiDate(new Date(data.modified_at)),
        }),
      )
    }
  }

  useEffect(() => {
    if (isVacancyPage) {
      if (!vacancyStudentToEdit) handleClose()
    } else if (!studentToEdit) handleClose()
  }, [studentToEdit?.id, vacancyStudentToEdit?.id])

  return (
    <div>
      <Dialog
        classes={{ paper: classes.root }}
        open={isVacancyPage ? isOpenVacancyAcceleratePopup : isOpenAcceleratePopup}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <div className={classes.container}>
          <IconButton onClick={handleClose} className={classes.closePopup}>
            <IconClose />
          </IconButton>
          <DialogContent className={classes.dialogContent}>
            <Typography className={classes.title}>Переход в Акселерацию</Typography>
            <Typography className={classes.subtitle}>
              Выбери дату, когда студент вступил в Акселерацию. Эти данные будут отображаться в DataLens
            </Typography>
            <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form-acceleration">
              <ControlledSelectField
                labelTitle="Студент"
                tipContent="Выбери студента"
                value={isVacancyPage ? vacancyStudentToEdit?.id : studentToEdit?.id}
                items={
                  isVacancyPage
                    ? [
                        {
                          id: vacancyStudentToEdit?.id,
                          name: `${vacancyStudentToEdit?.first_name} ${vacancyStudentToEdit?.last_name}`,
                        },
                      ]
                    : [{ id: studentToEdit?.id, name: `${studentToEdit?.first_name} ${studentToEdit?.last_name}` }]
                }
                disabled
              />
              <ControlledDateField
                labelTitle="Дата перехода в Акселерацию"
                fieldName="modified_at"
                dateValue={modifiedAt}
                error={!!errors.modified_at?.message}
                helperText={errors.modified_at?.message}
                onChange={dateChangeHandler}
              />
            </form>
            <Button form="form-acceleration" className={classes.submitButton} type="submit">
              Сохранить
            </Button>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}

export default React.memo(AccelerationPopup)
